<template>
    <div>
        
        <div class="header"></div>
        <div class="login-box">
            <!-- <div class="arrow-left" @click="closeRegister"> -->
            <div class="arrow-left" >
                
                <router-link to="/register">
                   <van-icon name="arrow-left" size="30px"/>                
                </router-link>
            </div>
            <div class="login-content">
                <div class="logo-box">
                    <div class="logo">用户头像</div>
                </div>

                <div class="userInformation">
                    <form>
                    <van-cell-group>
                        <!-- 用户名 --> 
                        <van-field
                            type="text"
                            placeholder="请输入用户名"
                            label="用户名:"
                            left-icon="contact"
                            v-model="loginData.username.value"
                            :error-message="loginData.username.errorMessage"
                            @input="inputUserName(loginData.username.value)"
                        />
                        
                        <!-- 密码 -->
                        <van-field
                            :type="loginData.password.type"
                            label="密码"
                            placeholder="请输入密码"
                            left-icon="lock"
                            :right-icon="loginData.password.icon"
                            v-model="loginData.password.value"
                            @click-right-icon="checkpossword1"
                            :error-message="loginData.password.errorMessage"
                            @input="inputPassword(loginData.password.value,loginData.username.value,loginData.password.errorMessage)"

                        />
                    </van-cell-group>

                    <div class="error">{{error}}</div>
                    <div class="login-button" >
                        <van-button class="login-btn" :loading="false" type="info" block loading-text="登录中..."  text="登录" :disabled="isdisabled" @click="login(loginData.username.value,loginData.password.value)" />
                    </div>
                    </form>
                    <div class="find-box"><span>忘记密码？</span><router-link to="login"><span class="find">找回密码</span></router-link></div>
                </div>
            </div>
        </div>
        <div>
            <router-view />
        </div>
    </div>
</template>

<script>
    export default {
        name:'login',

        data(){
            return{

                //判断是否开启注册按钮
                isdisabled:true,
                //登录提示
                error:'',

                //注册信息
                loginData:{
                    username:{
                        value:'',
                        errorMessage:''
                    },
                    password:{
                        value:'',
                        type:'password',
                        icon:'closed-eye',
                        errorMessage:''
                    },
                },

                isLogin:false
            }
        },

        methods:{

            // 密码是否可见
            checkpossword1(){
                let open1 = this.loginData.password.type ==='password'
                this.loginData.password.type = open1 ? 'text' : 'password'
                this.loginData.password.icon = open1 ? 'eye-o' : 'closed-eye'
            },

            // 验证用户名
            inputUserName(value){
                let usernamereg=/^[A-Za-z]\w{3,12}$/
                if(!usernamereg.test(value)){
                    this.loginData.username.errorMessage = '用户名格式错误(请用字母开头的3-12位字母或数字)'
                }else{
                    this.loginData.username.errorMessage = ''
                }
                this.error = ''
            },

            // 验证密码
            inputPassword(v1,v2,v3){
                let pwdReg = /^(?=.*[A-Z])(?=.*[a-z])\w{6,16}$/;
                if(!pwdReg.test(v1)){
                    this.loginData.password.errorMessage = '密码必须含有大写、小写、数字'
                }else{
                    this.loginData.password.errorMessage = ''
                }
                // 是否可以点击登录按钮
                if((v1 !='') && (v2 !='')){
                    this.isdisabled = false;
                }
                this.error = ''
            },

            login(v1,v2){

                let _this = this;
                // 从本地获取数据
                let userData = localStorage.getItem('userData');
                // console.log(userData)

                let randomTime = Math.random() * 3000;

                setTimeout( function (){
                    //获取所有用户信息
                    let userData = localStorage.getItem('userData');

                    userData = userData ? JSON.parse(userData) : [];
                    if(userData.length ===0){
                        this.$toast({
                            duration: 2000,
                            message: '该用户不存在'
                        });
                        return ;
                    }

                    //查找用户
                    for(let i=0; i<userData.length; i++){
                        if(userData[i].username ==v1){
                            //如果用户存在，则匹配密码
                            if(userData[i].password == v2){
                                _this.$toast({
                                    duration: 2000,
                                    message: '登录成功'
                                })

                                _this.isLogin = true;
                                // 保存在本地
                                localStorage.setItem('isLogin',JSON.stringify(_this.isLogin))
                                

                                setTimeout(function (){
                                  _this.$router.push({
                                    name:'mine',
                                    // params: {
                                    //     isLogin:_this.isLogin
                                    // }
                                  })  
                                },2000)                              
                                return;
                            }
                        }
                        else{
                            // _this.$toast({
                            //     duration: 2000,
                            //     message: '用户名或密码错误'
                            // })
                            _this.error = '用户名或密码错误';
                            return;
                        }
                    }
                    // 用户不存在
                    _this.$toast({
                        duration: 2000,
                        message: '该用户不存在'
                    })                   
                },randomTime)
            }
        }
    }
</script>

<style lang="less" scoped>
    .header{
        width: 100%;
        height: 24px;
    }
    .login-box{
    width: 100%;
    // height:17.8rem;
    background: #fff;
    z-index: 20;
    position:absolute;
    top: 0px;
    left: 0;
    // display: none
    margin-top: 24px;

    .arrow-left{
        height: .8rem;
        margin-left: .2rem;
    }

    .logo-box{
        width: 100%;
        height: 3rem;
        // background: #f5f5f5;
        position: relative;
        .logo{
            width: 2rem;
            height: 2rem;
            margin:0 auto;
            background: #ff3539;
            position:absolute;
            top: .5rem;
            left: 0;
            right: 0;
            border-radius: 50%;
            text-align: center;
            line-height: 2rem;
            font-size: .4rem;
            color: #fff;
        }
    }
    .userInformation{
        margin-top: .2rem;
        .login-button{
        margin-top: 1rem;
        }
    } 
    .find-box{
        margin-top: 1rem;
        font-size: .5rem;
        text-align: center;

        .find{
            color: #00B0F0;
            font-size: .6rem;
        }
    } 
    .error{
        width: 100%;
        height: .43rem;
        text-align: center;
        color: #ff3539;
    }

}
</style>